export const getFinalCommentDateText = (
  diffHours: number,
  hoursLimit: number,
  formattedDate: string
) => {
  if (diffHours > hoursLimit) {
    return formattedDate;
  }
  if (diffHours === 1 || diffHours % 10 === 1) {
    return `${diffHours}  `;
  }
  if (diffHours > 1 && diffHours < 5) {
    return `${diffHours}  `;
  }
  if (diffHours < 1) {
    return '  ';
  }
  return `${diffHours}  `;
};
